:root {
  --width-menu: 260px;
}
body {
  font-family: GT Eesti Pro Text;
}

.menu {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: calc(var(--width-menu));
  background-color: var(--color-black-1);
  -webkit-box-shadow: 11px 0px 13px -4px rgba(34, 60, 80, 0.2);
  -moz-box-shadow: 11px 0px 13px -4px rgba(34, 60, 80, 0.2);
  box-shadow: 11px 0px 13px -4px rgba(34, 60, 80, 0.2);
}

